.box {
  background-color: white;
  border: none;
  border-radius: 10%;
  box-shadow: 0px 0px 8px #888;
  width: 5rem;
  height: 5rem;
  text-align: center;
  font-weight: bold;
  line-height: 5rem;
  margin: 0.5rem;
  font-size: 50px;  
}

.x{
  color: blue;
}

.o{
  color: red;
}

.box:hover{
  box-shadow: 0px 0px 15px #888;
  cursor: pointer;
}